:root {
    --chat-row-template: 7.5rem 1fr 7.5rem;
    --room-link-label-font-size: 1.6rem;
    --composer-font-size: var(--room-link-label-font-size);
    --chat-line-font-size: var(--room-link-label-font-size);
    --chat-line-padding: 0.5rem 1rem;
}

@media (max-width: 960px) {
    /* Large Screens and below */
    :root {
        --chat-row-template: 5.5rem 1fr 7.5rem;
        --room-link-label-font-size: 1.4rem;
        --chat-line-padding: 0 1rem;
    }
}

/* Popup container - to display Room link is copied to clipboard */
.popup {
    position: relative;
    display: inline-block;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* The actual popup */
.popup .popuptext {
    visibility: hidden;
    width: 250px;
    background-color: #000;
    color: #fff;
    text-align: center;
    border-radius: 0.6rem;
    padding: 0.8rem 0;
    position: absolute;
    z-index: 1;
    bottom: -5rem;
}

/* Popup arrow */
.popup .popuptext::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -0.5rem;
}

.popup .show {
    visibility: visible;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
}

/* Chat */
.chat {
    background: white;
    display: grid;
    grid-template-rows: var(--chat-row-template);
}

.chat__wrapper .messages {
    overflow-x: hidden;
    overflow-y: scroll;
    height: calc(var(--vh, 1vh) * 100 - 335px);
}

.chat__room-link {
    display: inline-flex;
    place-content: center;
    place-items: center;
}

.chat-line__link {
    color: var(--color-text-link);
    text-decoration: underline;
}

.room-link {
    background: var(--color-bg-input);
    padding: 0.5rem 1.5rem;
    border-radius: 40px;
    display: inline-flex;
    place-content: center;
    place-items: center;
}

.room-link__label {
    color: var(--color-text-base);
    font-size: var(--room-link-label-font-size);
    padding: 0 1rem 0 0;
}

.chat-line {
	font: normal normal normal 16px/24px var(--font-sans);
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
    border-radius: 0;
    width: 100%;
    padding: 0;
    background: transparent;
	display: block;
    padding: 10px;
    font-family: 'Open Sans';
}

.chat-line .chat-action {
	display: flex;
    justify-content: space-between;
    align-items: center;
}

.chat-line .vote-count {
    margin-left: 0px;
}

.chat-line .border-bottom {
	width: 63%;
    border-bottom: 1px solid #D1D1D1;
	margin-left: 10px;
	margin-right: 0px;
}

.chime-web-composer {
    font-size: var(--composer-font-size);
    background: var(--color-bg-chat);
    padding: 1.5rem;
    height: 127px;
    background: #f0f0f0 0% 0% no-repeat padding-box;
    padding-left: 17px;
    padding-right: 17px;
    padding-bottom: 17px;
    bottom: 0px;
}

.chime-web-composer input,
.chime-web-composer textarea {
    background: #ffffff 0% 0% no-repeat padding-box;
    color: #6f7070;
    height: 75px;
    width: 100%;
    border-radius: unset;
    font-family: 'Open Sans';
}

.chime-web-composer .label {
    color: #00b5d9;
    text-align: left;
    font: normal normal bold 14px/21px var(--font-sans);
    letter-spacing: 0px;
    text-transform: uppercase;
    opacity: 1;
    margin-left: 5px;
}

.chime-web-composer textarea::placeholder {
    color: #6f7070;
}

.react-tabs__tab-list {
    border-bottom: none;
}

.react-tabs__tab-list .react-tabs__tab {
    font: normal normal bold 16px/25px var(--font-sans);
    letter-spacing: 0px;
    color: #d1d1d1;
    text-transform: uppercase;
    opacity: 1;
    border: none;
    padding: 16px 13px;
    outline: none;
    width: 49%;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    border-radius: 0;
    box-shadow: none;
}
.react-tabs__tab-list .react-tabs__tab.react-tabs__tab--selected {
	color: #00b5d9;
}

.participant-item{
    display: flex;
    padding: 10px;
    position: relative;
    align-items: center;
}

.raise-hand-icon {
    margin: 0 10px 0 0;
}
.participant-name {
    color: black;
    font-family: 'Open Sans';
}

.participant-tool-btns{
    display: none;
    background-color: white;
}

.participant-tool-btn,
.qa-tool-btn{
    display: flex;
    width: 30px;
    height: 30px;
    cursor: pointer;
    margin: 0 2px;
    background-repeat: no-repeat;
    background-position: center;
}

.participant-tool-btn:hover,
.qa-tool-btn:hover{
    border-radius: 50%;
    background-color: #e8e7e7;
    cursor: pointer;
}

.mute-icon {
    background-image: url(../../assets/svgs/host-mute.svg);
}

.host-icon{
    background-image: url(../../assets/svgs/host-host.svg);
}

.delete-icon{
    background-image: url(../../assets/svgs/bin.svg);
}

.vote-icon {
    background-image: url(../../assets/svgs/vote.svg);
}

.participant-item:hover .participant-tool-btns {
    display: flex;
    position: absolute;
    right: 0;
    top: 5px;
}

.message-container{
    height: calc(var(--vh, 1vh) * 100 - 207px);
    position: relative;
}
.chat-line-padding{
    padding-left: inherit;
}

.close-button{
    display: none;
}

.mobile-tab-title{
    display: none;
}

.mobile-submit-btn-container{
    display: none;
}

.mobile-question-input{
    display: none;
}

.part-raise-hand-btn{
    position: relative;
    display: flex;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    cursor: pointer;
}

.part-raise-hand-btn img{
    position: absolute;
    width: 20px;
    height: 20px;
}

.submit-question-label{
    font-family: 'Poppins', sans-serif!important;
    font-weight: 700; 
}

.chat-sender-name{
    font-family: 'Poppins', sans-serif!important;
    font-weight: 700; 
    color: #d1d1d1;
    font-size: 14px;
    text-transform: uppercase;
    padding-left: 10px;
}

@media only screen and (max-width: 1024px) {
    .chime-web-composer .label{
        font-family: 'Poppins', sans-serif;
        font-weight: 700;
    }

    .message-container{
        height: calc(var(--vh, 1vh) * 100 - 70px);
    }

    .close-button{
        color: #00b5d9;
        position: fixed;
        top: 24px;
        right: 20px;
        font-weight: 700;
        font-size: 16px;
        z-index: 100;
        display: flex;
        align-items: center;
        font-family: 'Poppins', sans-serif;
    }

    .close-button img{
        margin-right: 12px;
    }

    .chime-web-composer{
        margin: 10px;
        width: calc(100% - 20px);
        top: calc(var(--vh, 1vh) * 100 - 270px);
    }

    .ioschrome-input{
        top: calc(var(--vh, 1vh) * 100 - 370px)!important;
    }

    .desktop-tab{
        display: none;
    }

    .mobile-tab-title{
        display: block;
        color: #91278f;
        font-weight: 700;
        font-size: 20px;
        padding: 40px 0 0 20px;
        font-family: 'Poppins', sans-serif;
    }

    .participant-tool-btns{
        display: flex;
        position: absolute;
        right: 0;
        top: 5px;
    }

    .chat__wrapper .messages{
        height: calc(var(--vh, 1vh) * 100 - 210px);
    }

    .ioschrome-messages{
        height: calc(var(--vh, 1vh) * 100 - 319px)!important;
    }

    .desktop-question-input{
        display: none;
    }

    .mobile-question-input{
        display: block;
    }

    .mobile-submit-btn-container{
        display: flex;
        position: absolute;
        bottom: 0px;
        width: 100%;
        padding: 10px;
    }

    .ioschrome-input.mobile-submit-btn-container{
        top: calc(var(--vh, 1vh) * 100 - 235px)!important;
    }

    .mobile-submit-btn-container button{
        width: 100%;
        text-transform: uppercase;
        background: #00b5d9;
        color: white;
        font-size: 13px;
        border-radius: 0;
        border: 0;
        outline: none;
        font-family: 'Poppins', sans-serif;
        font-weight: 700;
    }
}