.alert-container{
    position: fixed;
    z-index: 98;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.alert-back-drop{
    position: absolute;
    z-index: 998;
    background-color: rgba(0, 0, 0, .5);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(15px);
    background-image: url(../../assets/alertbg.png);
    filter: blur(15px);
}

.alert-content{
    position: absolute;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items:center;
    font-family: 'Poppins', sans-serif; 
}

.alert-logo{
    margin-bottom: 66px;
}

.alert-content p{
    font-size: 24px;
    font-weight: bold;
    text-align: center;
}

.alert-content a {
    color: #00b5d9;
}

.ask-alert{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif; 
}

.ask-alert-backdrop{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    opacity: 0.5;
    background-color: white;
}

.ask-alert-container{
    z-index: 101;
}

.ask-alert-header{
    background: #91278f;
    color: white;
    padding: 17px 76px;
    font-size: 24px;
    text-align: center;
    font-weight: bold;
}

.ask-alert-content{
    display: flex;
    color: #000;
    justify-content: space-between;
    padding: 20px;
    background: white;
}

.ask-alert-item{
    border: solid 3px #f0f0f0;
    padding: 24px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    text-align: center;
}

.ask-alert-item img{
    width: 30px;
}

.ask-alert-item:hover{
    border: solid 3px #ddd;
}

.ask-alert-item:first-child{
    margin-right: 20px;
}

.ask-alert-item-text{
    font-size: 18px;
    margin-top: 20px;
    font-family: 'Open Sans';
}

.endsession-ask-item, .remove-ask-item{
    margin-right: 10px;
    border-color: #d93154;
}

@media only screen and (max-width: 1024px) {
    .alert-content p{
        font-size: 15px;
        padding: 0 15px;
    }

    .ask-alert-container{
        margin: 20px;
    }

    .ask-alert-content{
        flex-direction: column;
    }

    .ask-alert-header{
        padding: 12px 20px;
        font-size: 20px;
    }

    .ask-alert-item{
        width: 100%;
    }

    .ask-alert-item:first-child, .endsession-ask-item{
        margin-right: 0;
        margin-bottom: 20px;
    }

}