body {
    font-family: var(--font-sans);
}

.btnPopupMessage {
    position: absolute;
    background: #fafafa 0% 0% no-repeat padding-box;
    opacity: 1;
    bottom: 76px;
    /* min-height: 230px; */
    bottom: 76px;
    right: 0px;
    padding: 19px 21px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.vicon {}

.vleft {
    transform: rotate(90deg);
    margin: 0 10px 10px 10px;
}

.vright {
    transform: rotate(-90deg);
    margin: 0;
}

.btnPopupMessage:hover {
    padding-right: 27px;
}

.btnPopupMessage span {
    writing-mode: vertical-rl;
}

.btnPopupMessageFont {
    text-align: left;
    font: normal normal bold 16px/25px var(--font-sans);
    letter-spacing: 0px;
    color: #00b5d9;
    /*
    text-transform: uppercase;
    */
    opacity: 1;
    height: unset;
    border-radius: unset;
    box-sizing: border-box;
    border: unset;
    transition: padding ease 0.5s;
    font-family: 'Poppins', sans-serif;
}

.btnPopdownMessage {
    background: #fafafa 0% 0% no-repeat padding-box;
    opacity: 1;
    position: absolute;
    left: -41px;
    width: 43px;
    height: 50px;
    text-align: center;
    top: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.listPopupMessage {
    background-color: white;
    color: black;
    position: absolute;
    width: 30%;
    height: 100%;
    right: -70%;
    top: -740%;
}

.meeting-grid {}

.meeting-header {
    width: 100%;
    height: 76px;
    background: var(--midnight) 0% 0% no-repeat padding-box;
    background: #91278f 0% 0% no-repeat padding-box;
    opacity: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0 16px 24px;
}

.header-logo {
    opacity: 1;
    max-height: 100%;
}

.main-body {
    height: calc(var(--vh, 1vh) * 100 - 76px);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.body-left {
    background: var(--shadow) 0% 0% no-repeat padding-box;
    background: #7090B0 0% 0% no-repeat padding-box;
    border: unset;
    opacity: 1;
    width: 100%;
}

.body-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.body-logo {
    text-align: left;
    font: normal normal bold 14px/28px var(--font-sans);
    letter-spacing: 0px;
    color: #00b5d9;
    text-transform: uppercase;
    opacity: 1;
    padding: 20px 21px;
    background: white;
    display: inline-flex;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}

.body-logo img {
    /* width: 137px; */
    height: 37px;
    margin-left: 20px;
}

.body-before {
    height: calc(var(--vh, 1vh) * 100 - 151px);
}

.meeting-footer {
    width: 100%;
    height: 74px;
    background: var(--midnight) 0% 0% no-repeat padding-box;
    background: #91278f 0% 0% no-repeat padding-box;
    opacity: 1;
}

.right-sidebar {
    /* height: 100%;
    position: relative;
     */
    position: absolute;
    right: 0;
    height: calc(100vh - 150px);
    z-index: 2;
}

.right-sidebar .chat {
    width: 307px;
}

.remote-cams {
    display: flex;
    position: absolute;
    top: 0px;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    /* padding: 0 10%; */
}

.remote-cams-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.sharing-video-container {
    width: 100%;
    display: flex;
    /* justify-content: flex-start; */
    align-items: center;
}

.sharing-headline-container {
    background-color: #3fd09f;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    z-index: 1;
    left: 0;
    right: 0;
    height: 56px;

}

.sharing-text {
    font-family: 'Poppins', sans-serif;
    color: white;
    font-size: 18px;
    font-weight: bold;
}

.mycontent-stop-sharing {
    font-size: 13px;
    background: #ffffff 0% 0% no-repeat padding-box;
    opacity: 1;
    border-radius: 0;
    color: #3fd09f;
    font-family: 'Poppins', sans-serif;
    margin-left: 42px;
    font-weight: bold;
    padding: 12px 18px;
    white-space: nowrap;
    display: flex;
    align-items: center;
}

#share_video {
    width: 100%;
    height: calc(100vh - 206px) !important;
    margin-top: 56px;
}

.opened-width-share-video {
    width: calc(100% - 307px) !important;
}

.local-cam {
    margin: 0 !important;
    border-radius: 0;
    width: 128px !important;
    height: 69px !important;
    position: absolute;
    z-index: 2;
}

.btn--qa,
.btn--participants {
    display: none;
}

.mobile-buttons {
    display: none;
}

.display-none {
    display: none !important;
}

.join-video-mobile {
    background: #3FD09F 0% 0% no-repeat padding-box;
    opacity: 1;
    border-radius: 0;
}

@media only screen and (max-width: 1024px) {
    .meeting-header {
        background: #91278f;
        padding: 10px 0 10px 10px;
    }

    .header-logo {
        width: 115px;
    }

    .body-logo {
        font-size: 12px;
        padding: 20px 20px 20px 0;
    }

    .body-logo img {
        width: 115px;
    }

    .body-wrapper {
        /* padding-top: 73px; */
    }

    .remote-cams {
        top: 73px;
        height: calc(var(--vh, 1vh) * 100 - 224px);
        overflow-y: scroll;
    }



    .local-cam {
        top: 73px !important;
        width: 128px !important;
        height: 69px !important;
    }

    .btnPopupMessageFont {
        display: none;
    }

    .btn--share {
        display: none;
    }

    .btn--qa,
    .btn--participants {
        display: block;
    }

    .right-sidebar {
        /* position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%; */
    }

    .right-sidebar .chat {
        width: 100%;
        position: fixed !important;
        left: 0;
        top: 0;
        z-index: 2;
    }

    .meeting-footer {
        z-index: 3;
        height: 0;
    }

    .mobile-buttons {
        background: #761f74;
        padding: 16px;
        justify-content: flex-end;
        display: flex;
    }

    .sharing-text {
        font-size: 14px;
    }

    .sharing-headline-container {
        justify-content: space-evenly;
        padding: 10px;
    }

    .mycontent-stop-sharing {
        margin: 0;
        font-size: 13px;
    }

    #share_video {
        height: calc(100vh - 290px) !important;
    }
}