.go-livebtn {
    font-size: 14px;
    width: 120px;
    height: 42px;
    background: #3FD09F 0% 0% no-repeat padding-box;
    opacity: 1;
    transition: transform 0.3s;
    border-radius: 0;
    margin-right: 14px;
}

.live-meetingbtn{
    width: 140px;
    height: 42px;
    background: var(--unnamed-color-ec0b50) 0% 0% no-repeat padding-box;
    background: #EC0B50 0% 0% no-repeat padding-box;
    opacity: 1;
    transition: transform 0.3s;
    border-radius: 0;
}

.go-livebtn:hover,
.join-video-chat-now:hover {
    transform: translateY(-5px);
    background: #45ebb3;
}

.live-meetingbtn:hover{
    transform: translateY(-5px);
    background: #ff004e;
}

.txt-onbtn{
    color: #ffffff;
    text-align: center;
    letter-spacing: -0.13px;
    color: #FFFFFF;
    text-transform: uppercase;
    opacity: 1;
    font: inherit;
    font-size: 13px;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
}
.txt-oncontrolbtn{
    text-align: left;
    font: normal normal normal 16px/24px inherit;
    letter-spacing: 0px;
    color: #FFFFFF;
    opacity: 1;
    padding-left: 10px;
    padding-top: 6px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.button-group1{
    display: flex;
    flex-grow: 1;
    justify-content: space-evenly;
}
.button-group2{
    display: flex;
}
.button-group3{
    display: flex;
}


.button-layout{
    display: flex;
}

.btn-toggle{
    position: relative;
}

.btn-toggle .toggle-icon{
    transition: transform 0.3s;
}

.btn-toggle .un-icon{
    position: absolute;
}

.mute-block{
    left: -3px;
    top: 3px;
}

.camera-block{
    top: 2px;
    left: 0;
}

.share-block{
    top: 5px;
    left: 4px;
}

.hand-block{
    top: 5px;
    left: 0;
}

.btn-toggle .toggle-off-icon{
    opacity: 0.3;
}

.btn-toggle:hover .toggle-icon{
    transform: scale(1.2);
    opacity: 0.7;
}

.btn-toggle:hover .raise-toggle-icon{
    transform: scale(1.2) translateY(-5px);
}

.btn-toggle .toggle-text{
    min-width: 120px;
    font-family: 'Open Sans';
}

.btn-toggle:hover .toggle-text{
    text-decoration: underline;
}

.join-video-chat-now {
    background: #3FD09F 0% 0% no-repeat padding-box;
    opacity: 1;
    transition: transform 0.3s;
    border-radius: 0;
}

@media only screen and (max-width: 1024px) {
    .button-group3,
    .join-video-chat-now{
        display: none;
    }

    .button-layout {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .btn-toggle .toggle-text {
        text-align: center;
        padding: 0;
    }

    .controls__btn{
        margin: 0!important;
    }

    .btn-toggle .toggle-text{
        min-width: 0;
        font-size: 11px;
    }

    .btn-toggle .toggle-icon{
        width: 25px;
        height: 25px;
    }

    .button-group1{
        /* z-index: 10; */
        background: #91278f 0% 0% no-repeat padding-box;
        padding: 17px 0;
    }

    .btn-toggle .un-icon{
        width: 25px;
    }

    .mute-block{
        left: 10px;
        top: 1px;
    }

    .camera-block{
        top: 2px;
        left: 14px;
    }

    .hand-block{
        top: 2px;
        left: 16px;
    }

    .btn-toggle:hover .toggle-icon{
        transform: none;
    }
    
    .btn-toggle:hover .raise-toggle-icon{
        transform: none;
    }

    .txt-onbtn{
        font-size: 12px;
        font-weight: 700;
    }
}
