:root {
    --section-max-width: 700px;
    --color--primary: #fff;
    --color--secondary: #e7ecf0;
    --color--tertiary: #2d2d2d;
    --color-text-base: #fff;
    --color-text-hint: #e7ecf0;
    --color-text-link: #8bb0ff;
    --color-text-inverted: #000;
    --color-bg-input: #454545;
    --color-bg-button: var(--color-bg-input);
    --color-bg-base: #000;
    --color-bg-header: var(--color-bg-base);
    --color-bg-modal: var(--color-bg-base);
    --color-bg-modal-overlay: rgba(185, 185, 192, 0.9);
    --color-bg-chat: var(--color--tertiary);
    --color-bg-chat-bubble: var(--color--tertiary);
    --font-weight-bold: 600;
    --btn-size: 3.6rem;
    --form-col-template: minmax(380px, 460px) 1fr;
    --form-row-template: 1fr;
    --intro-text-align: left;
    --welcome-content-align-items: center;
    --app-col-template: 1fr minmax(330px, 340px);
    --app-row-template: 1fr;
}

@media (max-width: 960px) {
    /* Large Screens and below */
    :root {
        --form-col-template: 1fr;
        --form-row-template: auto 1fr;
        --intro-text-align: center;
        --welcome-content-align-items: flex-start;
        --app-col-template: 1fr;
        --app-row-template: 2fr minmax(200px, 1fr);
    }
}

input:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.app-grid {
    height: 100vh;
    position: relative;
    display: grid;
    grid-template-columns: var(--app-col-template);
    grid-template-rows: var(--app-row-template);
    overflow: hidden;
}

.form-grid {
    height: 100vh;
    position: relative;
    display: grid;
    grid-template-columns: var(--form-col-template);
    grid-template-rows: var(--form-row-template);
    overflow: hidden;
}

/* Intro */
.welcome{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: url("../../assets/bg.png");
    background-size: cover;
    background-position: left;
    background-repeat: no-repeat;
}

.welcome__intro {
    background: url("../../assets/bg.jpg");
    background-size: cover;
    text-align: var(--intro-text-align);
}

.intro__inner {
    padding: 3rem;
}

.welcome__content {
    display: flex;
    align-items: var(--welcome-content-align-items);
    align-content: center;
    /* justify-content: center; */
    text-align: var(--intro-text-align);
    padding: 0 20%;
}

.content__inner {
    padding-top: 83px;
    width: 100%;
}

.all-about-law {
    color: white;
    font-size: 55px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
}

.events-logo{
    width: 300px;
}

.events-title {
    font-size: 107px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    color: white;
    font-style: italic;
}

.join-as-host-part-title {
    margin-top: 71px;
    margin-bottom: 19px;
    font-family: 'Poppins', sans-serif;
    font-size: 28px;
}

.join-info-text {
    max-width: 444px;
    background-color: white;
    height: 53px!important;
    border-radius: 0;
    margin-bottom: 20px;
    color: #707070;
    font-family: 'Open Sans';
    font-size: 18px;
}

.join-btn {
    background-color: #ec0b8d;
    width: fit-content;
    color: white;
    border-radius: 0;
    height: 42px;
    padding: 0 15px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    font-size: 13px;
}

@media only screen and (max-width: 1024px) {
    .all-about-law {
        font-size: 36px;
        text-align: left;
    }

    .events-title {
        font-size: 70px;
        text-align: left;
    }

    .welcome__content {
        padding: 0 20px;
    }

    .join-as-host-part-title {
        text-align: left;
    }

    .join-btn {
        width: 100%;
        height: 54px;
    }

    .welcome{
        /* background-position-x: -900px; */
    }

    .join-info-text {
        max-width: 100%;
    }
}

/* Main stage */
.main-stage {
    /*
    display: grid;
    grid-template-rows: minmax(80px, 160px) 1fr auto;
    overflow-y: auto;
    */
}

.modal__overlay {
    z-index: 2;
}

.modal__el {
    z-index: 3;
}

/* Speaker */
.main-stage audio {
    display: none;
}

/* Cams */
.cams {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    padding: 1rem 0;
    overflow-x: auto;
}

.cam {
    height: 100%;
    /* flex: 0 1 210px; */
    background: var(--color--tertiary);
    margin: 0 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    position: relative;
    border-radius: var(--radius-small);
    width: 30%;
}

.remote-cams .cam{
    height: calc((var(--vh, 1vh) * 100 - 151px) /3 );
    margin: 0;
    width: calc(100% / 3 - 1px);
    border-radius: 0;
}

.cam-layout-1 {
    width: 100%!important;
    height: calc(100vh - 151px)!important;
}

.cam-layout-2 {
    width: 50%!important;
    height: calc(100vh - 151px)!important;
}

.cam-layout-3 {
    height: calc(100vh - 151px)!important;
}

.cam-layout-4,
.cam-layout-5,
.cam-layout-6 {
    height: calc(50vh - 75px)!important;
}

@media only screen and (max-width: 1024px) {
    .remote-cams .cam-layout-2,
    .remote-cams .cam-layout-3,
    .remote-cams .cam-layout-4,
    .remote-cams .cam-layout-5,
    .remote-cams .cam-layout-6,
    .remote-cams .cam-layout-7{
        height: calc((var(--vh, 1vh) * 100 - 224px) / 2)!important;
        width: 100%!important;
    }

    .cam-layout-1 {
        width: 100%!important;
        height: calc(var(--vh, 1vh) * 100 - 224px )!important;
    }

    .remote-bottom-bar{
        font-size: 12px;
        padding: 5px;
    }
}

.cam__preview {
    flex-grow: 1;
    width: 100%;
    height: 100%;
}

.remote-bottom-bar{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    background: #91278f;
    opacity: 0.75;
    color: white;
    text-align: center;
    font-family: 'Open Sans';
}

.cam__preview .video-container,
.cam__preview .attendee_cam {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.video-container {
    background-image: url("data:image/png;base64,");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 48px 48px;
}

.cam__meta {
    position: absolute;
    bottom: 0.5rem;
    display: inline-flex;
    place-items: center;
    font-size: 1.2rem;
    font-weight: var(--font-weight-bold);
    background: rgba(0, 0, 0, 0.8);
    padding: 0.5rem 1rem;
    border-radius: var(--radius-small);
    z-index: 2;
    opacity: 1;
    transform: translate(0, 0);
}

.cam__meta--hide {
    opacity: 0;
    transform: translate(0, 1rem);
    transition: opacity 0.25s ease, transform 0.25s ease;
}

/* Player */
.player-wrapper {
    width: 100%;
    background: black;
    position: relative;
    overflow: hidden;
}

.aspect-spacer {
    /*
    padding-bottom: 56.25%;
    */
}

.el-player {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: #000;
}

/* Controls */
.controls {
    display: flex;
    justify-content: space-between;
    padding: 15px;
}

@media only screen and (max-width: 1024px) {
    .controls {
        padding: 0px;
    }   
}

.controls__btn {
    /*
    width: 44px;
    height: 44px;
    */
    margin: 0 1rem;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.btn__svg {
    fill: var(--color-text-base);
}

.btn__svg--sm {
    height: 16px;
    width: 16px;
    position: relative;
    top: 2px;
}

/* .controls__btn--mic_on .btn__svg--mic_on {
    display: none;
}

.controls__btn--mic_off .btn__svg--mic_off {
    display: none;
} */

/* .controls__btn--cam_on .btn__svg--cam_on {
    display: none;
}
.controls__btn--cam_off .btn__svg--cam_off {
    display: none;
} */

.controls .controls__btn--cam_on,
.controls .controls__btn--mic_on {
    background-color: transparent;
}

/* Overwrites */
input,
select {
    color: var(--color-text-hint);
}

a {
    color: var(--color-text-base);
    font-weight: 600;
}

a.btn {
    display: block;
    line-height: calc(var(--input-height) - var(--btn-border-size));
    text-align: center;
    border: 2px solid transparent;
    border-radius: var(--radius-small);
}

.btn--primary {
    color: var(--color-text-inverted);
}

.hidden {
    display: none !important;
}

.btn:disabled {
    opacity: 0.5;
}

.select__field {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url("data:image/svg+xml;utf8,<svg fill='white' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 100%;
    background-position-y: 5px;
}

/* Player Overwrites */

#overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
}

#player-controls {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    transition: background 0.1s ease-in-out;
}

.player-controls__inner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.overlay--hover #player-controls {
    background: rgba(0, 0, 0, 0.5);
}

.overlay--hover .player-btn {
    display: flex;
}

.player-btn {
    outline: none;
    appearance: none;
    cursor: pointer;
    border: 2px solid transparent;
    -webkit-appearance: none;
    background: transparent;
    bottom: 10px;
    padding: 0;
    display: flex;
    flex-shrink: 0;
    flex-wrap: nowrap;
    display: none;
    border-radius: 2px;
}

.player-btn--icon {
    width: var(--btn-size);
    height: var(--btn-size);
    justify-content: center;
    align-items: center;
}

.player-btn:focus {
    border: 2px solid #fff;
}

.player-icon {
    fill: #fff;
}

.player-btn--pause .player-icon--pause {
    display: none;
}

.player-btn--play .player-icon--play {
    display: none;
}

.player-btn--mute .player-icon--volume_off {
    display: none;
}

.player-btn--unmute .player-icon--volume_up {
    display: none;
}
